import React from 'react';
import '../css/GameMenu.css';
import playpause from '../assets/image/playpause.svg'
import one from '..//assets/image/icon/one.svg'
import two from '..//assets/image/icon/two.svg'
import three from '..//assets/image/icon/three.svg'
import four from '..//assets/image/icon/four.svg'
import five from '..//assets/image/icon/five.svg'
import six from '..//assets/image/icon/six.svg'


class GameMenu extends React.Component{
    constructor(){
        super();
        this.state={
            icon:one,
            val:-1,
            result:"",
            count:0
        }

        this.time=0;
        this.val=this.state.val;
        this.count=0;

    }

    componentDidMount(){
        this.time = setInterval(()=>{
            this.state.val=Math.floor(Math.random()*5)+1;

            if(this.state.val==1){
                this.setState({
                    icon:one
                })

            }else if(this.state.val==2){
                this.setState({
                    icon:two
                })
            }else if(this.state.val==3){
                this.setState({
                    icon:three
                })
            }else if(this.state.val==4){
                this.setState({
                    icon:four
                })
            }else if(this.state.val==5){
                this.setState({
                    icon:five
                })
            }else if(this.state.val==6){
                this.setState({
                    icon:six
                })
            }
            this.count++;
            this.setState({count:this.count})
        },200)
    }  
    componentWillUnmount(){
        clearInterval(this.time);
       
    }  

    render(){
        if(this.props.dice==this.state.val){
            clearInterval(this.time);
         
        }


        const styles = {
            fontSize:this.props.fontSize,
            filter:this.props.filter
        }
        
        return(
            <div className="game-menu"  prevmenu="main-menu">
                <div className="game-menu-list-item" style={styles}  data="1" value="play-game">One</div>
                <div className="game-menu-list-item" style={styles}  data="2" value="play-game">Two</div>
                <div className="game-menu-list-item" style={styles}  data="3" value="play-game">Three</div>
                <div className="game-menu-list-item" style={styles}  data="4" value="play-game">Four</div>
                <div className="game-menu-list-item" style={styles}  data="5" value="play-game">Five</div>
                <div className="game-menu-list-item" style={styles}  data="6" value="play-game">Six</div>

                
                <div id="game-result">
                    <div>Catch a number</div>

                    <img id="dice-display" style={styles}  src={this.state.icon} />
                    
                    <div>Your Current Time</div>
                    <div>{this.state.count}</div>
                    <div id="restart">
                        <span>Press </span>
                        <img id="again" src={playpause} />
                        <span>to Restart</span>
                    </div>
                </div>

       

     
            </div>



        )
    }


    
}

export default GameMenu;
 
